<template>
    <div>
        <Navbar title="商品列表"></Navbar>

        <div class="seckilling-con" id="seckill_index"> 
            <ul class="clearfix">
                <li v-for="(item,index) in detail" :key="item.id" @click="setGood(index)">
                    <router-link :to='{name:"shopdetail",query:{id:item._id}}'>
                        <p class="pic"><img class="" :src="item.url"></p>
                        <p class="price">
                            <span class="rob">
                                <span class="num">{{item.price}}</span>
                                <span class="tail"></span>
                            </span>
                        </p>
                    </router-link>
                </li>
            </ul>
        </div>       
        <div class="bottom12"></div>
    </div>
</template>

<script>
export default {
    name:'shop',
    data(){
        return {
            detail:""
        }
    },
    beforeCreate(){
        this.$axios({
            url:`api/follow`,
            params:{_page:1,_limit:24}
        }).then(res=>{
            this.detail=res.data.data
            
        })
    },
    methods:{
        setGood(i){
            console.log(this.detail[i])
            this.$store.dispatch('goodByAction',this.detail[i]);
        }
    }
}
</script>
<style scoped>
.seckilling-con li{float: left;width: 33%;margin-top:2rem;border:.01rem solid #aaa}
.seckilling-con li.line1{margin-left:0.5%;}
.seckilling-con li a{padding:0 .25rem;display: block;}
.seckilling-con li a img{display: block;width: 100%;}
.seckilling-con li .name{padding: 0 .25rem;font-size: 1.6rem;height: 1rem;line-height: 1rem;white-space: nowrap;text-overflow: ellipsis;overflow: hidden;}
.seckilling-con li .price{padding: .1rem 0 .2rem;color: #fe435d;font-size: .475rem;}
.seckilling-con li .price .rob{font-size: 1rem;}
.seckilling-con li .price .discount{display:inline-block;float: right;overflow:hidden;margin-top:.05rem;width:1.5rem;height:.625rem;background-color:#fe435d;color: #fff;font-size: .475rem;text-align: center;-webkit-border-radius:.1rem;border-radius:.1rem;}
.bottom12{
    float: left;
    width: 100%;
    height: 5rem
}
</style>
